#hobbies {
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}

.hobbyTitle {
    margin: 1rem 0;
    font-size: 3rem;
}

.hobbysDesc {
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;
}

.hobbysImgs {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}

.hobbysImg {
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
}

.hobbyBtn {
    padding: 0.5rem 2.5rem;
    margin: 3rem 0;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: 1rem;
    cursor: pointer;
}

.hobbyBtn:hover {
    background: lightgray;
}

@media screen and (max-wdith: 585px) {
    .hobbysImg {
        height: 48vw;
    }
}